import { gql, useMutation, useReactiveVar } from "@apollo/client";
import { useEffect } from "react";
import { isMobile } from "react-device-detect";
import { useHistory } from "react-router-dom";
import { Var_applicantData } from "../Payment1_1/Lib_applicantFormData";
import { visualPaymentDataVar } from "../Service_visual2/Var_priceData";
import TagManager from "react-gtm-module";

export default function App() {
  const history = useHistory();

  // 쿠키파싱
  const newApplicantData = JSON.parse(
    window.localStorage.getItem("applicantDataVar") ||
      JSON.stringify(Var_applicantData())
  );
  Var_applicantData(newApplicantData);

  const serviceVisualClickedData = JSON.parse(
    window.localStorage.getItem("serviceVisualClickedData") ||
      JSON.stringify("1")
  );
  const newVisualPaymentData = visualPaymentDataVar().map((val, idx) =>
    idx === +serviceVisualClickedData
      ? { ...val, clicked: true }
      : { ...val, clicked: false }
  );
  visualPaymentDataVar(newVisualPaymentData);

  // url search 정보에서 데이터 파싱
  const searchArray = history.location.search.replace("?", "").split("&");
  const paymentData = {
    orderId: searchArray[0]?.split("=")[1],
    paymentKey: searchArray[1]?.split("=")[1],
    amount: Number(searchArray[2]?.split("=")[1]),
  };

  // 로컬스토리지에서 결제 데이터 파싱
  const localPaymentData = JSON.parse(
    window.localStorage.getItem("service_paymentData") ||
      JSON.stringify({ orderId: "", amount: 0 })
  );

  // 결제승인정보 전송
  const PAYMENT_APPROVAL = gql`
    mutation {
      approvePayment(
        orderId:"${paymentData.orderId}"
        paymentKey:"${paymentData.paymentKey}"
        amount:${paymentData.amount}
      )
    }
  `;
  const [approvePaymentMutation] = useMutation(PAYMENT_APPROVAL);

  // 메일발송 정보
  const selectedPaymentData = useReactiveVar(visualPaymentDataVar).filter(
    (val) => val.clicked === true
  )[0];
  const applicantData = useReactiveVar(Var_applicantData);

  // 액션팀에 전송
  const SENDMAIL_TO_ACTIONTEAM = gql`
    mutation {
      sendPaymentMailToAction(
        from: "poketing_mail_server@poketing.com"
        to: "action@poketing.com"
        subject: "[공홈] 결제 완료/캠페인 의뢰 전달"
        serviceName:"비주얼 인플루언서 콘텐츠 마케팅"
        planName:"${
          selectedPaymentData.titleRaw +
          "<br>" +
          selectedPaymentData.mainText
            .map((val, idx) => "✔ " + val.title + " " + val.account)
            .join("<br>") +
          "<br><br> 결제금액: " +
          selectedPaymentData.totalPrice_num +
          "원"
        }"
        brandName:"${applicantData[0].state}"
        name:"${applicantData[1].state}"
        phoneNumber:"${applicantData[2].state}"
        email:"${applicantData[3].state}"
      )
    }
  `;
  const [sendMailMutationToActionteam] = useMutation(SENDMAIL_TO_ACTIONTEAM);

  // 클라이언트에 전송
  const SENDMAIL_TO_CLIENT = gql`
    mutation {
      sendPaymentMailToClient(
        from: "action@poketing.com"
        to: "${applicantData[3].state}"
        subject: "[포케팅] 결제 확인/캠페인 필수 안내사항 확인 부탁 드립니다."
        serviceName:"비주얼 인플루언서 콘텐츠 마케팅"
        planName:"${
          selectedPaymentData.titleRaw +
          "<br>" +
          selectedPaymentData.mainText
            .map((val, idx) => "✔ " + val.title + " " + val.account)
            .join("<br>")
        }"
        brandName:"${applicantData[0].state}"
        name:"${applicantData[1].state}"
        phoneNumber:"${applicantData[2].state}"
        email:"${applicantData[3].state}"
      )
    }
  `;
  const [sendMailMutationToClient] = useMutation(SENDMAIL_TO_CLIENT);

  useEffect(() => {
    //결제로직
    const approvePayment = async () => {
      setTimeout(() => {}, 1000);
      try {
        if (
          paymentData.orderId === localPaymentData.orderId &&
          paymentData.amount === localPaymentData.amount
        ) {
          await approvePaymentMutation();

          //결제 성공 로직
          const newVisualPaymentData = visualPaymentDataVar().map((val, idx) =>
            val.totalPrice_num === paymentData.amount
              ? { ...val, clicked: true }
              : { ...val, clicked: false }
          );
          await visualPaymentDataVar(newVisualPaymentData);
          await sendMailMutationToActionteam();
          await sendMailMutationToClient();

          await history.replace("/orderSheet/result");
        } else {
          throw "결제오류";
        }
      } catch {
        alert(
          "결제가 실패하였습니다.\n다시 한 번 시도하면\n대부분 정상적으로 진행됩니다.\n다시 한 번 시도해 주세요!\n\n혹시 다시 시도 시에 실패한다면\n우측 하단의 채팅 문의 주시면\n신속하게 도와 드리겠습니다. "
        );
        history.replace("/orderSheet");
      }
    };
    approvePayment();
  }, []);
  return <></>;
}
