import { useEffect, useState } from "react";
import { visualPaymentDataVar } from "../Service_visual2/Var_priceData";
import { Var_applicantData } from "../Payment1_1/Lib_applicantFormData";
import TagManager from "react-gtm-module";
import { useReactiveVar } from "@apollo/client";
import { throttle } from "throttle-debounce";

export default function App() {
  const selectedPaymentData = useReactiveVar(visualPaymentDataVar).filter(
    (val) => val.clicked === true
  )[0];
  useEffect(() => {
    // 쿠키파싱
    const applicantData = JSON.parse(
      window.localStorage.getItem("applicantDataVar") ||
        JSON.stringify(Var_applicantData())
    );
    Var_applicantData(applicantData);

    const serviceVisualClickedData = JSON.parse(
      window.localStorage.getItem("serviceVisualClickedData") ||
        JSON.stringify("1")
    );
    const visualPaymentData = visualPaymentDataVar().map((val, idx) =>
      idx === +serviceVisualClickedData
        ? { ...val, clicked: true }
        : { ...val, clicked: false }
    );
    visualPaymentDataVar(visualPaymentData);
  }, []);

  const [Trigger1, setTrigger1] = useState(false);
  const scrollYCheck = () => {
    if (window.scrollY > 0) {
      setTrigger1(true);
    }
  };
  const throttleScrollYCheck = throttle(200, scrollYCheck);
  useEffect(() => {
    window.addEventListener("scroll", throttleScrollYCheck);
    return () => window.removeEventListener("scroll", throttleScrollYCheck);
  }, []);

  useEffect(() => {
    // 구글이벤트
    TagManager.dataLayer({
      dataLayer: {
        event: "Purchase",
        eventModel: {
          content_category: "비주얼 인플루언서 콘텐츠 마케팅",
          content_name: selectedPaymentData.titleRaw,
          value: selectedPaymentData.totalPrice_num,
          currency: "KRW",
        },
      },
    });
  }, [Trigger1]);

  return <></>;
}
