import { useEffect, useRef, useState } from "react";
import { isMobile } from "react-device-detect";
import { throttle } from "throttle-debounce";
import Consulting1_1 from "../Consulting1_1";
import AveragePerfomance from "../Service_visual2/Org_averagePerfomance";
import CostomerReact from "../Service_visual2/Org_costomerReact";
import FAQ from "../Service_visual2/Org_FAQ";
