import LastMessage from "../Service_visual2/Org_lastMessage";
import Partner from "../Service_visual2/Org_partner";
import PerformancePrinciple from "../Service_visual2/Org_performancePrinciple";
import PricePlan_v2 from "../Service_visual2/Org_pricePlan_v2";
import PricePlan_v3 from "../Service_visual2/Org_pricePlan_v3";
import Proposal1 from "../Service_visual2/Org_proposal1";
import Proposal2 from "../Service_visual2/Org_proposal2";
import Proposal3 from "../Service_visual2/Org_proposal3";
import Ref1 from "../Service_visual2/Org_ref1";
import Sequence from "../Service_visual2/Org_sequence";
import SwipeVideo1 from "../Service_visual2/Org_swipeVideo1";
import VisualIs from "../Service_visual2/Org_visualIs";
import Sec_1showVideo from "./Sec_1showVideo";
import Sec_floating from "./Sec_floating";

const data1 = (
  <>
    평균 11배 매출 상승시킨
    <br />
    비주얼 인플루언서 콘텐츠로
    <br />
    상세페이지와 광고의 {isMobile ? <br /> : <></>}
    성과를 높이세요.
  </>
);

const data_poketingIs = {
  title: [
    <>
      원칙을 지키고 “파트너 성장”에 <br />
      집중한 포케팅은
    </>,
  ],
  subtitle: [],
  lists: [
    {
      title: (
        <>
          <span>성과 개선 한</span>{" "}
          <span className="font-bold">상세페이지 수</span>
        </>
      ),
      mainText: [35, ",000"],
    },
    {
      title: (
        <>
          <span>성과 개선 한</span> <span className="font-bold">광고 수</span>
        </>
      ),
      mainText: [110, ",000"],
    },
    {
      title: (
        <>
          <span>인스타그램</span>{" "}
          <span className="font-bold">인기게시글 수</span>
        </>
      ),
      mainText: [63, ",000"],
    },
  ],
};

export default function App() {
  const [scrollCheck1, setScrollCheck1] = useState(false);
  const element1: any = useRef();
  const [scrollCheck2, setScrollCheck2] = useState(false);
  const element2: any = useRef();
  const [scrollCheck3, setScrollCheck3] = useState(false);
  const element3: any = useRef();

  const scrollYCheck = () => {
    const { current: current1 }: any = element1;
    const element1Coordinate = current1.getBoundingClientRect().top;
    if (element1Coordinate + 300 < window.innerHeight) {
      setScrollCheck1(true);
    } else {
      setScrollCheck1(false);
    }
    const { current: current2 }: any = element2;
    const element2Coordinate = current2.getBoundingClientRect().top;
    if (element2Coordinate + 300 < window.innerHeight) {
      setScrollCheck2(true);
    } else {
      setScrollCheck2(false);
    }
    const { current: current3 }: any = element3;
    const element3Coordinate = current3.getBoundingClientRect().top;
    if (element3Coordinate + 150 < window.innerHeight) {
      setScrollCheck3(true);
    } else {
      setScrollCheck3(false);
    }
  };

  const throttleScrollYCheck = throttle(200, scrollYCheck);
  useEffect(() => {
    window.addEventListener("scroll", throttleScrollYCheck);
    return () => window.removeEventListener("scroll", throttleScrollYCheck);
  }, []);

  return isMobile ? (
    // 모바일
    <section>
      <Sec_floating visible={!scrollCheck3} />

      <div className="w-11/12 mx-auto my-10">
        <div className=" text-2xl font-bold">{data1}</div>
      </div>

      <div className="mb-6"></div>
      <Sec_1showVideo></Sec_1showVideo>
      <div className="mb-14"></div>

      <div ref={element1}>
        <AveragePerfomance trigger={scrollCheck1}></AveragePerfomance>
      </div>
      <CostomerReact></CostomerReact>
      <VisualIs></VisualIs>
      <Proposal1></Proposal1>
      <Proposal2></Proposal2>
      <Proposal3></Proposal3>
      <Ref1></Ref1>
      <PerformancePrinciple></PerformancePrinciple>
      <div ref={element2}>
        <AveragePerfomance
          data={data_poketingIs}
          trigger={scrollCheck2}
        ></AveragePerfomance>
      </div>
      <Partner></Partner>
      <Sequence></Sequence>
      <div ref={element3}>
        <Consulting1_1></Consulting1_1>
      </div>
    </section>
  ) : (
    // 피씨
    // 피씨
    // 피씨
    // 피씨
    // 피씨
    // 피씨
    <section className="">
      <Sec_floating />
      <div className=" max-w-screen-lg mx-auto my-20 flex justify-center">
        <div className=" text-3xl font-bold text-center leading-normal">
          {data1}
        </div>
      </div>
      <div className="max-w-screen-lg mx-auto pb-10">
        <div className="mb-6"></div>
        <Sec_1showVideo></Sec_1showVideo>
        <div className="mb-14"></div>
      </div>
      <div ref={element1}>
        <AveragePerfomance trigger={scrollCheck1}></AveragePerfomance>
      </div>
      <CostomerReact></CostomerReact>
      <VisualIs></VisualIs>
      <Proposal1></Proposal1>
      <Proposal2></Proposal2>
      <Proposal3></Proposal3>
      <Ref1></Ref1>
      <PerformancePrinciple></PerformancePrinciple>
      <div ref={element2}>
        <AveragePerfomance
          data={data_poketingIs}
          trigger={scrollCheck2}
        ></AveragePerfomance>
      </div>
      <Partner></Partner>
      <Sequence></Sequence>
      <div ref={element3}>
        <Consulting1_1></Consulting1_1>
      </div>
    </section>
  );
}
