import { useReactiveVar } from "@apollo/client";
import TagManager from "react-gtm-module";
import { useEffect } from "react";
import { PushDataLayer } from "../../../hooks/PushDataLayer";
import { Var_consultingInputData } from "../Consulting1_1/Lib_consultingForm";

export default function App() {
  const consultingInputData = useReactiveVar(Var_consultingInputData);
  useEffect(() => {
    const newConsultingInputData = JSON.parse(
      window.localStorage.getItem("consultingInputData") ||
        JSON.stringify(consultingInputData)
    );
    Var_consultingInputData(newConsultingInputData);

    // 구글이벤트
    TagManager.dataLayer({
      dataLayer: {
        event: "Contact",
        eventModel: {
          content_name: "비주얼 인플루언서 콘텐츠 마케팅",
        },
      },
    });
  }, []);

  return <></>;
}
